import React, { useState } from "react";
import { Container, Nav, ProgressBar } from "react-bootstrap";
import CDCAgreed from "./Agreed/CDCAgreed";
import CDCDenied from "./Denied/CDCDenied";
function CDCPastAction() {
  const [NavProgressBar, setNavProgressBar] = useState(0);
  const [Tab, setTab] = useState("agreed");
  return (
    <div>
      <Container>
        <div className="my-2">
          <Nav>
            <Nav.Item
              className="w-50 text-center"
              style={{ backgroundColor: "#c7f7c7" }}
              onClick={() => {
                setNavProgressBar(0);
                setTab("agreed");
              }}
            >
              <Nav.Link className="text-dark mx-2">Agreed</Nav.Link>
            </Nav.Item>
            <Nav.Item
              className="w-50 text-center"
              style={{ backgroundColor: "#f7c7c7" }}
              onClick={() => {
                setNavProgressBar(50);
                setTab("denied");
              }}
            >
              <Nav.Link className="text-dark mx-2">Denied</Nav.Link>
            </Nav.Item>
          </Nav>
          <ProgressBar
            style={{
              backgroundColor: "#f8f9fa",
              height: "3px",
              width: "100%",
            }}
          >
            <ProgressBar variant="light" now={NavProgressBar} key={1} />
            <ProgressBar variant="primary" now={50} key={2} />
          </ProgressBar>
        </div>
      </Container>
      {Tab === "agreed" ? (
        <CDCAgreed />
      ) : Tab === "denied" ? (
        <CDCDenied />
      ) : null}
    </div>
  );
}

export default CDCPastAction;
