import React, { useState } from "react";
import { Button, Form, Modal } from "react-bootstrap";
import axios from "axios";

function ResetPassword({ PopChangePassword, setPopChangePassword, id }) {
  const [OldPassword, setOldPassword] = useState("");
  const [NewPassword, setNewPassword] = useState("");
  const [ConfirmPassword, setConfirmPassword] = useState("");
  function handleSubmit(e) {
    e.preventDefault();
    axios
      .post("https://college-project-node.herokuapp.com/change_password", {
        id: id,
        old_password: OldPassword,
        password: NewPassword,
        confirm_password: ConfirmPassword,
      })
      .then((res) => {
        if (res.data.message === "password_changed") {
          setPopChangePassword(false);
          alert("Your Password has been changed Successfully");
        } else {
          alert(res.data.message);
        }
      });
  }
  return (
    <>
      <Modal
        show={PopChangePassword}
        onHide={() => {
          setPopChangePassword(false);
        }}
      >
        <Modal.Header closeButton>
          <Modal.Title>Change Password</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label>Old Password</Form.Label>
              <Form.Control
                type="password"
                placeholder="Password"
                onChange={(e) => {
                  setOldPassword(e.target.value);
                }}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label>New Password</Form.Label>
              <Form.Control
                type="password"
                placeholder="Password"
                onChange={(e) => {
                  setNewPassword(e.target.value);
                }}
              />
              <Form.Control
                type="password"
                placeholder="Confirm Password"
                onChange={(e) => {
                  setConfirmPassword(e.target.value);
                }}
              />
            </Form.Group>

            <Button
              variant="primary"
              type="submit"
              onClick={(e) => {
                handleSubmit(e);
              }}
            >
              Confirm Password
            </Button>
          </Form>
        </Modal.Body>
        <Modal.Footer></Modal.Footer>
      </Modal>
    </>
  );
}

export default ResetPassword;
