import "../App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import SearchIcon from "../img/search.png";
import { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import { useEffect } from "react";

function Search() {
  const [search, setSearch] = useState();
  const [Session, setSession] = useState(null);
  const [Dept, setDept] = useState(null);

  const [SelectSession, setSelectSession] = useState([]);
  const [SelectDept, setSelectDept] = useState([]);

  const history = useHistory();
  function loadDeptSession() {
    axios
      .post("https://college-project-node.herokuapp.com/unique_session")
      .then((res) => {
        setSelectSession(res.data);
      });
    axios
      .post("https://college-project-node.herokuapp.com/unique_dept")
      .then((res) => {
        setSelectDept(res.data);
      });
  }
  useEffect(() => {
    loadDeptSession();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div
      className="d-flex justify-content-center align-items-center bodycss"
      style={{ height: "93%" }}
    >
      <div className="container">
        <div className="row d-flex justify-content-center ">
          <h1 className="fontstyle text-white">GMITFinder</h1>
        </div>
        <div className="row">
          <div className="col">
            <div className="search_wrap search_wrap_over fontstyle">
              <div className="search_box">
                <input
                  type="text"
                  className="input"
                  placeholder="Skill Search..."
                  onChange={(e) => {
                    setSearch(e.target.value);
                  }}
                  onKeyDown={(ev) => {
                    if (ev.key === "Enter") {
                      history.push(`search/${search}/${Dept}/${Session}`);
                    }
                  }}
                />
                <Link
                  to={`search/${search}/${Dept}/${Session}`}
                  className="btn btn_common"
                >
                  <img
                    src={SearchIcon}
                    alt="SearchIcon"
                    style={{ height: "75%", paddingTop: "30%" }}
                  />
                </Link>
              </div>
            </div>
          </div>
        </div>
        <div className="row  ">
          <div className="col-md-3"></div>
          <div className="col">
            <select
              className="form-control"
              onChange={(e) => {
                setDept(e.target.value);
              }}
              aria-label="Default select example"
            >
              <option disabled selected>
                Select Department
              </option>
              {SelectDept.map((info, index) => {
                return (
                  <option key={index} value={info}>
                    {info}
                  </option>
                );
              })}
            </select>
          </div>
          <div className="col">
            <select
              className="form-control"
              onChange={(e) => {
                setSession(e.target.value);
              }}
              aria-label="Default select example"
            >
              <option disabled selected>
                Select Session
              </option>
              {SelectSession.map((info, index) => {
                return (
                  <option key={index} value={info}>
                    {info}
                  </option>
                );
              })}
            </select>
          </div>
          <div className="col-md-3"></div>
        </div>
      </div>
    </div>
  );
}

export default Search;
