import axios from "axios";
import React, { useState } from "react";
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import { Facebook, Instagram, Whatsapp, Youtube } from "react-bootstrap-icons";

export default function ContactUs() {
  const [Name, setName] = useState("");
  const [Phone, setPhone] = useState("");
  const [Email, setEmail] = useState("");
  const [Message, setMessage] = useState("");

  function handleSubmit() {
    axios
      .post("https://college-project-node.herokuapp.com/insertcontact", {
        name: Name,
        email: Email,
        phone: Phone,
        message: Message,
      })
      .then((res) => {
        if (res.data.message === "inserted") {
          alert("We Will Contact You As Soon As Possible");
        } else {
          alert("Something went wrong");
        }
      });
  }

  return (
    <div className="bodycss py-2 ">
      <Container className="bg-light py-3 rounded">
        <Row className="bg-success text-white">
          <Col>
            <h1 className="text-center">Contact Us</h1>
          </Col>
        </Row>

        <Row>
          <Col md="6" className="py-5">
            <h4>Send us message</h4>
            <Form>
              <Form.Group
                className="my-3"
                controlId="exampleForm.ControlInput1"
              >
                <Form.Control
                  type="text"
                  placeholder="Your Name"
                  onChange={(e) => {
                    setName(e.target.value);
                  }}
                />
              </Form.Group>
              <Form.Group
                className="my-3"
                controlId="exampleForm.ControlInput1"
              >
                <Form.Control
                  type="text"
                  placeholder="Your Phone Number"
                  onChange={(e) => {
                    setPhone(e.target.value);
                  }}
                />
              </Form.Group>
              <Form.Group
                className="my-3"
                controlId="exampleForm.ControlInput1"
              >
                <Form.Control
                  type="email"
                  placeholder="Your Email"
                  onChange={(e) => {
                    setEmail(e.target.value);
                  }}
                />
              </Form.Group>
              <Form.Group
                className="my-3"
                controlId="exampleForm.ControlTextarea1"
              >
                <Form.Control
                  as="textarea"
                  rows={3}
                  placeholder="Your Message"
                  onChange={(e) => {
                    setMessage(e.target.value);
                  }}
                />
              </Form.Group>
              <Button
                className="w-100"
                onClick={() => {
                  handleSubmit();
                }}
              >
                Submit
              </Button>
            </Form>
          </Col>
          <Col md="6" className="py-5 text-center">
            <Button variant="primary" className="rounded rounded-circle">
              <Facebook />
            </Button>
            {"  "}
            <Button variant="danger" className="rounded rounded-circle">
              <Youtube />
            </Button>
            {"  "}
            <Button variant="primary" className="rounded rounded-circle">
              <Instagram />
            </Button>
            {"  "}
            <Button variant="success" className="rounded rounded-circle">
              <Whatsapp />
            </Button>
            {"  "}
            <br />
            <h5 className="text-center mt-3">Ph: 9786875758</h5>
            <h5 className="text-center mt-3">Email: ijazur@gmail.com</h5>
            <iframe
              title="map"
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3689.3532040127116!2d88.43875281536896!3d22.37803844572254!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3a026d0097533aed%3A0xe18407a46f2e97f6!2sGargi%20Memorial%20Institute%20of%20Technology!5e0!3m2!1sen!2sin!4v1633153241179!5m2!1sen!2sin"
              width="100%"
              height="300px"
              style={{ border: 0 }}
              allowfullscreen=""
              loading="lazy"
            ></iframe>
          </Col>
        </Row>
      </Container>
    </div>
  );
}
