import "../App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { useParams } from "react-router";
import { Button, Col, Row, Table } from "react-bootstrap";
import { useState, useEffect } from "react";
import axios from "axios";
import HandleUserDetails from "../redux/logincheck";
// import {Link} from 'react-router-dom';

function ProfileViewer() {
  const { id } = useParams();
  const [profileInfo, setProfileInfo] = useState();
  const [skillInfo, setSkillInfo] = useState();
  const [academicInfo, setAcademicInfo] = useState();
  const [AchievementInfo, setAchievementInfo] = useState();
  const [projectInfo, setProjectInfo] = useState();
  const [WishlistExist, setWishlistExist] = useState(false);

  var userinfo = HandleUserDetails();

  const profileData = () => {
    axios
      .post("https://college-project-node.herokuapp.com/allcvdata", {
        id: id,
      })
      .then((dbData) => {
        setProfileInfo(dbData.data[0]);
        if (userinfo && userinfo.user_type === "HR") {
          axios
            .post(
              "https://college-project-node.herokuapp.com/hr_wishlist_exist",
              {
                hr_id: userinfo.id,
                student_id: dbData.data[0]._id,
              }
            )
            .then((dbData) => {
              setWishlistExist(dbData.data.exist);
            });
        }
      });
  };

  const acadData = () => {
    axios
      .post("https://college-project-node.herokuapp.com/getacademic", {
        user_id: id,
      })
      .then((dbData) => {
        setAcademicInfo(dbData.data);
      });
  };

  const skillData = () => {
    axios
      .post("https://college-project-node.herokuapp.com/getskill", {
        user_id: id,
      })
      .then((dbData) => {
        setSkillInfo(dbData.data);
      });
  };
  const achieveData = () => {
    axios
      .post("https://college-project-node.herokuapp.com/getachievement", {
        user_id: id,
      })
      .then((dbData) => {
        setAchievementInfo(dbData.data);
      });
  };
  const projectData = () => {
    axios
      .post("https://college-project-node.herokuapp.com/getproject", {
        user_id: id,
      })
      .then((dbData) => {
        setProjectInfo(dbData.data);
      });
  };

  function addToWishlist() {
    axios
      .post("https://college-project-node.herokuapp.com/hr_wishlist_add", {
        hr_id: userinfo.id,
        student_id: profileInfo._id,
      })
      .then((dbData) => {
        axios
          .post(
            "https://college-project-node.herokuapp.com/hr_wishlist_exist",
            {
              hr_id: userinfo.id,
              student_id: profileInfo._id,
            }
          )
          .then((dbData) => {
            setWishlistExist(dbData.data.exist);
          });
      });
  }

  function removeToWishlist() {
    axios
      .post("https://college-project-node.herokuapp.com/hr_wishlist_remove", {
        hr_id: userinfo.id,
        student_id: profileInfo._id,
      })
      .then((dbData) => {
        axios
          .post(
            "https://college-project-node.herokuapp.com/hr_wishlist_exist",
            {
              hr_id: userinfo.id,
              student_id: profileInfo._id,
            }
          )
          .then((dbData) => {
            setWishlistExist(dbData.data.exist);
          });
      });
  }

  useEffect(() => {
    profileData();
    acadData();
    skillData();
    achieveData();
    projectData();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <>
      <div className="fontstyle bodycss p-2" style={{ minHeight: "93%" }}>
        <div className="container bg-light rounded">
          {profileInfo ? (
            <>
              {userinfo ? (
                userinfo.user_type === "HR" ? (
                  <>
                    {WishlistExist ? (
                      <Button
                        className="mt-2"
                        variant="danger"
                        onClick={() => {
                          removeToWishlist();
                        }}
                      >
                        Remove WishList
                      </Button>
                    ) : (
                      <Button
                        className="mt-2"
                        variant="success"
                        onClick={() => {
                          addToWishlist();
                        }}
                      >
                        Add WishList
                      </Button>
                    )}
                  </>
                ) : null
              ) : null}
              <Row className="d-flex justify-content-center py-3">
                <h3
                  style={{ backgroundColor: "#2c2a88", color: "white" }}
                  className="d-flex justify-content-center w-100"
                >
                  Web CV
                </h3>
              </Row>
              <Row className="d-flex justify-content-center p-3">
                <Col md="12" className="d-flex  align-items-center">
                  <div className=" mx-3">
                    <img
                      src={profileInfo.profile_pic}
                      alt="profile pic"
                      width="160px"
                      className="rounded rounded-circle "
                    />
                  </div>
                  <h4>{profileInfo.name}</h4>
                </Col>
              </Row>
              <Row
                style={{ backgroundColor: "#ba58ff" }}
                className="text-white px-3 mx-1"
              >
                Presonal Information
              </Row>
              <Row className="p-4">
                <Col>
                  Phone: {profileInfo.phone}
                  <br />
                  Email: {profileInfo.email}
                  <br />
                  Date of Birth:{" "}
                  {profileInfo.date_of_birth
                    .toString()
                    .split("T")[0]
                    .split("-")[2] +
                    "/" +
                    profileInfo.date_of_birth
                      .toString()
                      .split("T")[0]
                      .split("-")[1] +
                    "/" +
                    profileInfo.date_of_birth
                      .toString()
                      .split("T")[0]
                      .split("-")[0]}
                  <br />
                  Gender: {profileInfo.gender}
                  <br />
                  Father Name: {profileInfo.father_name}
                  <br />
                  Address: {profileInfo.address} <br />
                  GitHub: {profileInfo.github} <br />
                  LinkedIn: {profileInfo.linkedin} <br />
                  Department: {profileInfo.dept} <br />
                  Session: {profileInfo.session} <br />
                </Col>
              </Row>
              <Row
                style={{ backgroundColor: "#ba58ff" }}
                className="text-white px-3 mx-1"
              >
                Academic Information
              </Row>
              <Row className="text-white px-3 mx-1">
                <Table responsive striped borderless hover>
                  <thead>
                    <tr>
                      <th>Examination Name</th>
                      <th>Organization Name</th>
                      <th>Starting Date</th>
                      <th>Ending Date</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Array.isArray(academicInfo)
                      ? academicInfo.length !== 0
                        ? academicInfo.map((result, index) => {
                            return (
                              <tr key={index}>
                                <td>{result.degree}</td>
                                <td>{result.org_name}</td>
                                <td>
                                  {result.start_date
                                    ? result.start_date.split("T")[0]
                                    : null}
                                </td>
                                <td>{result.end_date.split("T")[0]}</td>
                              </tr>
                            );
                          })
                        : null
                      : null}
                  </tbody>
                </Table>
              </Row>
              <Row
                style={{ backgroundColor: "#ba58ff" }}
                className="text-white px-3 mx-1"
              >
                Skill Information
              </Row>
              <Row className="text-white px-3 mx-1">
                <Table responsive striped borderless hover>
                  <thead>
                    <tr>
                      <th>Skill Name</th>
                      <th>Learned In</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Array.isArray(skillInfo)
                      ? skillInfo.length !== 0
                        ? skillInfo.map((result, index) => {
                            return (
                              <tr key={index}>
                                <td>{result.skill}</td>
                                <td>{result.date.split("T")[0]}</td>
                              </tr>
                            );
                          })
                        : null
                      : null}
                  </tbody>
                </Table>
              </Row>
              <Row
                style={{ backgroundColor: "#ba58ff" }}
                className="text-white px-3 mx-1"
              >
                Achievement Information
              </Row>
              <Row className="text-white px-3 mx-1">
                <Table responsive striped borderless hover>
                  <thead>
                    <tr>
                      <th>Achievement Name</th>
                      <th>Event Name</th>
                      <th>Description</th>
                      <th>Date</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Array.isArray(AchievementInfo)
                      ? AchievementInfo.length !== 0
                        ? AchievementInfo.map((result, index) => {
                            return (
                              <tr key={index}>
                                <td>{result.topic}</td>
                                <td>{result.org_name}</td>
                                <td>{result.note}</td>
                                <td>{result.date.split("T")[0]}</td>
                              </tr>
                            );
                          })
                        : null
                      : null}
                  </tbody>
                </Table>
              </Row>
              <Row
                style={{ backgroundColor: "#ba58ff" }}
                className="text-white px-3 mx-1"
              >
                Project Information
              </Row>
              <Row className="text-white px-3 mx-1">
                <Table responsive striped borderless hover>
                  <thead>
                    <tr>
                      <th>Project Name</th>
                      <th>Description</th>
                      <th>Project Link</th>
                      <th>Git Link</th>
                      <th>Start Date</th>
                      <th>End Date</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Array.isArray(projectInfo)
                      ? projectInfo.length !== 0
                        ? projectInfo.map((result, index) => {
                            return (
                              <tr key={index}>
                                <td>{result.topic}</td>
                                <td>{result.note}</td>
                                <td>{result.project_link}</td>
                                <td>{result.git_link}</td>
                                <td>{result.start_date.split("T")[0]}</td>
                                <td>{result.end_date.split("T")[0]}</td>
                              </tr>
                            );
                          })
                        : null
                      : null}
                  </tbody>
                </Table>
              </Row>
            </>
          ) : null}
        </div>
      </div>
    </>
  );
}
export default ProfileViewer;
