import axios from "axios";
import React, { useState } from "react";
import { Button, Form, Modal } from "react-bootstrap";

function ChangeProfilePic({
  PopChangeProfilePic,
  setPopChangeProfilePic,
  profileData,
  id,
}) {
  const [ImgPath, setImgPath] = useState("");
  function handleImg(e) {
    e.preventDefault();
    let img = e.target.files[0];
    if (!img) return alert("File not exist.");
    //5242880 == 5 mb
    if (img.size > 1024 * 1024 * 10) return alert("Size too large!");
    if (img.type !== "image/jpeg" && img.type !== "image/png")
      return alert("File format is incorrect.");

    setImgPath(img);
  }

  function handleSubmit() {
    let data = new FormData();
    data.append("file", ImgPath);
    data.append("upload_preset", "mir9232384204");
    data.append("cloud_name", "ddwroqk9k");
    fetch("https://api.cloudinary.com/v1_1/ddwroqk9k/image/upload", {
      method: "post",
      body: data,
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
        axios
          .post(
            "https://college-project-node.herokuapp.com/update_profile_pic",
            {
              id: id,
              profile_pic: data.url,
            }
          )
          .then((res) => {
            alert(res.data.message);
            setPopChangeProfilePic(false);
            profileData();
          });
      })
      .catch((err) => {
        console.log(err);
      });
  }

  return (
    <div>
      <Modal
        show={PopChangeProfilePic}
        onHide={() => setPopChangeProfilePic(false)}
      >
        <Modal.Header closeButton>
          <Modal.Title>Select Profile Picture</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Group controlId="formFile" className="mb-3">
            <Form.Control
              type="file"
              className="border border-primary rounded"
              onChange={(e) => {
                handleImg(e);
              }}
            />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant="primary"
            onClick={() => {
              handleSubmit();
              setPopChangeProfilePic(false);
              profileData();
            }}
          >
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default ChangeProfilePic;
