import "../App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import axios from "axios";

function ProfileCard(props) {
  const [userdata, setUserData] = useState();
  function getUserdata() {
    axios
      .post("https://college-project-node.herokuapp.com/profiledata", {
        id: props.id,
      })
      .then((dbData) => {
        setUserData(dbData.data[0]);
        console.log(dbData.data[0]);
      });
  }
  useEffect(() => {
    getUserdata();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  if (userdata) {
    if (props.dept === "null" && props.session === "null") {
      console.log("2 null");
      return (
        <>
          {userdata ? (
            <Link
              to={`/profileviewer/${userdata._id}`}
              style={{ textDecoration: "none", color: "inherit" }}
            >
              <div className="card mb-3">
                {userdata ? (
                  <div className="row no-gutters">
                    <div className="col-md-2 text-center ">
                      <img
                        src={userdata.profile_pic}
                        alt="profile pic"
                        className="rounded img-fluid "
                      />
                    </div>
                    <div className="col-md-10">
                      <div className="card-body">
                        <h5 className="card-title">{userdata.name}</h5>

                        <p className="card-text">
                          {userdata.dept} {userdata.session}
                        </p>
                        <p className="card-text">{userdata.email}</p>
                        <p className="card-text">{userdata.phone}</p>
                        <h5 className="card-text">
                          According to your search we found <b>{props.skill}</b>{" "}
                          as result
                        </h5>
                      </div>
                    </div>
                  </div>
                ) : null}
              </div>
            </Link>
          ) : (
            <h1 className="d-flex justify-content-center text-white">
              Loading....
            </h1>
          )}
        </>
      );
    } else if (props.dept !== "null" && props.session === "null") {
      console.log("ses null");
      return (
        <>
          {userdata ? (
            userdata.dept === props.dept ? (
              <Link
                to={`/profileviewer/${userdata._id}`}
                style={{ textDecoration: "none", color: "inherit" }}
              >
                <div className="card mb-3">
                  {userdata ? (
                    <div className="row no-gutters">
                      <div className="col-md-2 text-center ">
                        <img
                          src={userdata.profile_pic}
                          alt="profile pic"
                          className="rounded img-fluid "
                        />
                      </div>
                      <div className="col-md-10">
                        <div className="card-body">
                          <h5 className="card-title">{userdata.name}</h5>

                          <p className="card-text">
                            {userdata.dept} {userdata.session}
                          </p>
                          <p className="card-text">{userdata.email}</p>
                          <p className="card-text">{userdata.phone}</p>
                          <h5 className="card-text">
                            According to your search we found{" "}
                            <b>{props.skill}</b> as result
                          </h5>
                        </div>
                      </div>
                    </div>
                  ) : null}
                </div>
              </Link>
            ) : null
          ) : (
            <h1 className="d-flex justify-content-center text-white">
              Loading....
            </h1>
          )}
        </>
      );
    } else if (props.dept === "null" && props.session !== "null") {
      console.log("dept null");
      return (
        <>
          {userdata ? (
            userdata.session === props.session ? (
              <Link
                to={`/profileviewer/${userdata._id}`}
                style={{ textDecoration: "none", color: "inherit" }}
              >
                <div className="card mb-3">
                  {userdata ? (
                    <div className="row no-gutters">
                      <div className="col-md-2 text-center ">
                        <img
                          src={userdata.profile_pic}
                          alt="profile pic"
                          className="rounded img-fluid "
                        />
                      </div>
                      <div className="col-md-10">
                        <div className="card-body">
                          <h5 className="card-title">{userdata.name}</h5>

                          <p className="card-text">
                            {userdata.dept} {userdata.session}
                          </p>
                          <p className="card-text">{userdata.email}</p>
                          <p className="card-text">{userdata.phone}</p>
                          <h5 className="card-text">
                            According to your search we found{" "}
                            <b>{props.skill}</b> as result
                          </h5>
                        </div>
                      </div>
                    </div>
                  ) : null}
                </div>
              </Link>
            ) : null
          ) : (
            <h1 className="d-flex justify-content-center text-white">
              Loading....
            </h1>
          )}
        </>
      );
    } else {
      return (
        <>
          {userdata ? (
            userdata.dept === props.dept &&
            userdata.session === props.session ? (
              <Link
                to={`/profileviewer/${userdata._id}`}
                style={{ textDecoration: "none", color: "inherit" }}
              >
                <div className="card mb-3">
                  {userdata ? (
                    <div className="row no-gutters">
                      <div className="col-md-2 text-center ">
                        <img
                          src={userdata.profile_pic}
                          alt="profile pic"
                          className="rounded img-fluid "
                        />
                      </div>
                      <div className="col-md-10">
                        <div className="card-body">
                          <h5 className="card-title">{userdata.name}</h5>

                          <p className="card-text">
                            {userdata.dept} {userdata.session}
                          </p>
                          <p className="card-text">{userdata.email}</p>
                          <p className="card-text">{userdata.phone}</p>
                          <h5 className="card-text">
                            According to your search we found{" "}
                            <b>{props.skill}</b> as result
                          </h5>
                        </div>
                      </div>
                    </div>
                  ) : null}
                </div>
              </Link>
            ) : null
          ) : (
            <h1 className="d-flex justify-content-center text-white">
              Loading....
            </h1>
          )}
        </>
      );
    }
  } else {
    return null;
  }
}
export default ProfileCard;
