import axios from "axios";
import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useHistory } from "react-router";
import HandleUserDetails from "../redux/logincheck";
import WishlistCard from "./WishlistCard";

function WishList() {
  const [WishlistData, setWishlistData] = useState([]);
  var userInfo = HandleUserDetails();
  var history = useHistory();

  function loadData() {
    if (userInfo) {
      axios
        .post("https://college-project-node.herokuapp.com/hr_wishlist_data", {
          hr_id: userInfo.id,
        })
        .then((res) => {
          setWishlistData(res.data);
          console.log(res.data);
        });
    } else {
      history.push("/");
    }
  }

  useEffect(() => {
    loadData();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div className="fontstyle bodycss p-2" style={{ minHeight: "75%" }}>
      <Container className=" bg-light" style={{ minHeight: "400px" }}>
        <Row className="d-flex justify-content-center py-3">
          <h3
            style={{ backgroundColor: "#2c2a88", color: "white" }}
            className="d-flex justify-content-center w-100"
          >
            WishList
          </h3>
        </Row>
        <Row>
          {WishlistData.map((info, i) => {
            return (
              <Col md="6" key={i}>
                <WishlistCard student_id={info.student_id} />
              </Col>
            );
          })}
        </Row>
      </Container>
    </div>
  );
}

export default WishList;
