import axios from "axios";
import React, { useEffect, useState } from "react";
import { Button, Col, Modal, Row, Table } from "react-bootstrap";

function CDCViewContactInfo() {
  const [MessageData, setMessageData] = useState([]);
  const [ViewMessage, setViewMessage] = useState(null);

  function handleLoad() {
    axios
      .post("https://college-project-node.herokuapp.com/get_contact")
      .then((res) => {
        setMessageData(res.data);
      });
  }
  useEffect(() => {
    window.addEventListener("load", handleLoad());
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div>
      <Modal
        show={ViewMessage ? true : false}
        onHide={() => {
          setViewMessage(null);
        }}
      >
        <Modal.Header closeButton>
          <Modal.Title>View Message</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <h4>Name: {ViewMessage ? ViewMessage.name : null}</h4>
          <h5>Phone: {ViewMessage ? ViewMessage.phone : null}</h5>
          <h5>Email: {ViewMessage ? ViewMessage.email : null}</h5>
          <div
            style={{ border: "1px blue solid", borderRadius: "8px" }}
            className="px-2"
          >
            <h5>Message:</h5>
            <p> {ViewMessage ? ViewMessage.message : null}</p>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <a href={`tel:${ViewMessage ? ViewMessage.phone : null}`}>
            <Button variant="success">Call</Button>
          </a>
          <a
            href={`mailto:${
              ViewMessage ? ViewMessage.email : null
            }?subject=Reply from GMIT About Your Query&body=Dear ${
              ViewMessage ? ViewMessage.name : null
            },\n`}
          >
            <Button>Reply Mail</Button>
          </a>
          <Button
            variant="danger"
            onClick={() => {
              setViewMessage(null);
            }}
          >
            Close
          </Button>
        </Modal.Footer>
      </Modal>
      <Row>
        <Col>
          <h1>Messages</h1>
        </Col>
      </Row>
      <Row>
        <Col>
          <div className="bg-primary pt-1 w-100"></div>
        </Col>
      </Row>
      <Row>
        <Col>
          <Table striped responsive hover>
            <thead>
              <tr>
                <th>Name</th>
                <th>Email</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {MessageData.map((info, index) => {
                return (
                  <tr key={index}>
                    <td>{info.name}</td>
                    <td>{info.email}</td>
                    <td>
                      <Button
                        variant="info"
                        onClick={() => {
                          setViewMessage(info);
                        }}
                      >
                        View Message
                      </Button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </Table>
        </Col>
      </Row>
    </div>
  );
}

export default CDCViewContactInfo;
