import "bootstrap/dist/css/bootstrap.css";
import { Container, Row, Form, Col, Button } from "react-bootstrap";
import { useState, useEffect } from "react";
import HandleUserDetails from "../../redux/logincheck";
import axios from "axios";
import ResetPassword from "./ResetPassword";

function EditInformation(props) {
  const [name, setName] = useState("");
  const [emailName, setEmailName] = useState("");
  const [fatherName, setFatherName] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [dob, setDob] = useState("");
  const [gender, setGender] = useState("");
  const [gitHub, setGitHub] = useState("");
  const [linkedIn, setLinkedIn] = useState("");
  const [session, setSession] = useState("");
  const [dept, setDept] = useState("");

  const [PopChangePassword, setPopChangePassword] = useState(false);

  var userinfo = HandleUserDetails("");

  const profileData = () => {
    axios
      .post("https://college-project-node.herokuapp.com/allcvdata", {
        id: userinfo.id,
      })
      .then((dbData) => {
        setName(dbData.data[0].name);
        setEmailName(dbData.data[0].email);
        setFatherName(dbData.data[0].father_name);
        setPhone(dbData.data[0].phone);
        setAddress(dbData.data[0].address);
        setDob(dbData.data[0].date_of_birth);
        setGender(dbData.data[0].gender);
        setGitHub(dbData.data[0].github);
        setLinkedIn(dbData.data[0].linkedin);
        setSession(dbData.data[0].session);
        setDept(dbData.data[0].dept);
      });
  };
  useEffect(() => {
    profileData();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  function handleInsert() {
    axios
      .post("https://college-project-node.herokuapp.com/insertyourinfo", {
        id: userinfo.id,
        name: name,
        phone: phone,
        email: emailName,
        dob: dob,
        gender: gender,
        father_name: fatherName,
        address: address,
        github: gitHub,
        linkedin: linkedIn,
        session: session,
        dept: dept,
      })
      .then((data) => {
        props.setSideNavBarOption("your_details");
      });
  }

  return (
    <>
      <ResetPassword
        PopChangePassword={PopChangePassword}
        setPopChangePassword={setPopChangePassword}
        id={userinfo.id}
      />
      <Container fluid className="pb-4">
        <Row className="d-flex justify-content-center py-3">
          <h3
            style={{ backgroundColor: "#2c2a88", color: "white" }}
            className="d-flex justify-content-center w-100"
          >
            Edit Presonal Information
          </h3>
        </Row>
        <Row className="d-flex justify-content-center p-3">
          <Col md="6">
            <Form.Control
              value={name}
              onChange={(e) => {
                setName(e.target.value);
              }}
              placeholder="Your Name"
            />
          </Col>
          <Col md="6">
            <Form.Control
              value={emailName}
              onChange={(e) => {
                setEmailName(e.target.value);
              }}
              placeholder="Your Email"
            />
          </Col>
        </Row>
        <Row className="d-flex justify-content-center p-3">
          <Col md="4">
            <Form.Control
              value={fatherName}
              onChange={(e) => {
                setFatherName(e.target.value);
              }}
              placeholder="Your Father Name"
            />
          </Col>
          <Col md="4">
            <Form.Control
              value={phone}
              onChange={(e) => {
                setPhone(e.target.value);
              }}
              placeholder="Your Phone"
            />
          </Col>
          <Col md="4">
            <Form.Control
              value={address}
              onChange={(e) => {
                setAddress(e.target.value);
              }}
              placeholder="Your Address"
            />
          </Col>
        </Row>
        <Row className="d-flex justify-content-center p-3">
          <Col md="6">
            <Form.Label>Your Date of Birth</Form.Label>
            <Form.Control
              value={dob}
              onChange={(e) => {
                setDob(e.target.value);
              }}
              type="date"
            />
          </Col>
          <Col md="6">
            <Form.Label>Your Gender</Form.Label>
            <select
              className="form-control"
              onChange={(e) => {
                setGender(e.target.value);
              }}
              defaultValue={gender}
            >
              <option value="" disabled></option>
              <option value="Male">Male</option>
              <option value="Female">Female</option>
            </select>
          </Col>
        </Row>
        <Row className="d-flex justify-content-center p-3">
          <Col md="6">
            <Form.Control
              value={gitHub}
              onChange={(e) => {
                setGitHub(e.target.value);
              }}
              placeholder="GitHub Id"
            />
          </Col>
          <Col md="6">
            <Form.Control
              value={linkedIn}
              onChange={(e) => {
                setLinkedIn(e.target.value);
              }}
              placeholder="LinkedIn Id"
            />
          </Col>
        </Row>
        <Row className="d-flex justify-content-center p-3">
          <Col md="6">
            <select
              className="form-control"
              onChange={(e) => {
                setDept(e.target.value);
              }}
              defaultValue={dept}
            >
              <option value="" disabled>
                Select Your Department
              </option>
              <option value="CE">CE</option>
              <option value="CSE">CSE</option>
              <option value="ECE">ECE</option>
              <option value="EE">EE</option>
              <option value="ME">ME</option>
            </select>
          </Col>
          <Col md="6">
            <select
              className="form-control"
              onChange={(e) => {
                setSession(e.target.value);
              }}
              defaultValue={session}
            >
              <option value="" disabled>
                Select Your Session
              </option>
              <option value="2013-2017">2013-2017</option>
              <option value="2014-2018">2014-2018</option>
              <option value="2015-2019">2015-2019</option>
              <option value="2016-2020">2016-2020</option>
              <option value="2017-2021">2017-2021</option>
              <option value="2018-2022">2018-2022</option>
              <option value="2019-2023">2019-2023</option>
              <option value="2020-2024">2020-2024</option>
            </select>
          </Col>
        </Row>
        <Row>
          <Button
            className="mx-4"
            onClick={() => {
              handleInsert();
            }}
          >
            Change
          </Button>
          <Button
            className="mx-4"
            variant="info"
            onClick={() => {
              setPopChangePassword(true);
            }}
          >
            Change Password
          </Button>
        </Row>
      </Container>
    </>
  );
}
export default EditInformation;
